.App {
  text-align: center;
}
.f-w {
  width: 100%;
  padding-left: 0;
  padding-right: 0;
}
.hero-bg {
  background: url(https://www.mapitstudio.com/wp-content/uploads/2021/04/didelis-medinis-zemlapis-sieninis-pasaulio-zemelapis-su-saliu-pavadinimais-ir-smeigtukais-azuolas-map-it-studio.jpg)
    top center;
  background-size: cover;
}
.hero {
  width: 100%;
  height: 80vh;
}
.hero-skeleton {
  width: 100%;
  height: 80vh;
  background-size: cover;
  margin-bottom: 50px;
}
.overlay {
  display: flex;
  width: 100%; /* Full width (cover the whole page) */
  height: 100%; /* Full height (cover the whole page) */
  background-color: rgba(0, 0, 0, 0.5); /* Black background with opacity */
  align-items: center;
  justify-content: center;
}
.grid-item {
  background-color: #f2f6fa;
}
.statuses {
  bottom: 20%;
}

.news-inside {
  height: 100%;
}
.news-outside {
  height: 30rem;
}
.news-outside ::-webkit-scrollbar {
  -webkit-appearance: none;
  width: 7px;
}

.news-outside ::-webkit-scrollbar-thumb {
  border-radius: 4px;
  background-color: rgba(0, 0, 0, 0.5);
  box-shadow: 0 0 1px rgba(255, 255, 255, 0.5);
}

.public-map .plus-icon {
  font-size: 100px;
}
.single-industry .plus-icon {
  font-size: 30px;
}

@media (min-width: 992px) {
  .navbar-expand-lg .navbar-collapse-b {
    display: block !important;
    flex-basis: auto;
  }
  .navbar-expand-lg .navbar-collapse-b ul {
    float: right;
  }
}
.grid-icon {
  font-size: 80px;
}
